<template>
  <div class="page-invite">
    <div class="card-top clearfix">
      <p v-if="cardsInfo.CardCount==0" class="card-title">
        邀请函已用完
      </p>
      <p v-else class="card-title">
        邀请函
      </p>
      <p class="card-link">
        <router-link to="/card-record" style="color: #111;">查看发卡记录</router-link>
      </p>
    </div>
    <div v-if="cardsInfo.CardCount==0" style="background-color: white;">
      <img class="no-card" :src="BackgroundImage">
      <p v-if="!cardsInfo.IsSendMessage" class="remind" @click.once="remindSR">提醒代表补卡</p>
      <p v-else class="remind" style="font-size: 14px;color: #9FA3AB;width:auto;background-color: initial;" v-html="remindResult"></p>
    </div>
    <div v-if="cardsInfo.CardCount>0" class="card-area" style="padding-top: 1px;" @click.stop="refresh">
      <p class="whiteBall" style="left: 2.5%;"></p>
      <p class="whiteBall" style="right: 2.5%;"></p>
      <p style="text-align: left; font-size: 18px;margin-left: 10%;margin-bottom: 8vw;">礼来优行邀请函剩余数量 {{cardsInfo.CardCount}}</p>
      <p class="qrcode-area" style="margin: auto 12%;">
        <img class="qrcode-img" :src="cardsInfo.PatientRegQrCode.data" />
        </p>
      <p style="text-align: center;">二维码每隔三分钟自动更新</p>
      <p style="text-align: center;">手动点击刷新</p>
    </div>
    <Footer />
    <div class="qrcode-loading" v-show="showLoading">
      <Loading />
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import Footer from '../../Shared/Footer/Footer'
import Loading from '../../Shared/Loading/LoadingPartial'
import BackgroundImage from '../../../assets/Card _ 2.jpg'
export default {
  name: 'Invitation',
  components: {
    Footer,
    Loading
  },
  data () {
    return {
      intervalId: 0,
      showLoading: false,
      BackgroundImage: BackgroundImage
    }
  },
  watch: {
    cardsInfo: function (val, oldVal) {
      this.showLoading = true
      setTimeout(() => {
        this.showLoading = (JSON.stringify(val) === '{}')
      }, 500)
    }
  },
  computed: {
    ...mapGetters({
      cardsInfo: 'cardsInfo',
      remindResult: 'remindResult'
    })
  },
  methods: {
    refresh () {
      clearInterval(this.intervalId)
      this.clearCard()
      this.refreshCard()
      this.intervalId = setInterval(this.refreshCard, 1000 * 60 * 3)
    },
    refreshCard () {
      this.showLoading = true
      this.$store.dispatch('refreshCard')
    },
    clearCard () {
      this.$store.dispatch('clearCard')
    },
    remindSR () {
      this.$store.dispatch('remindSR')
    }
  },
  beforeMount () {
    this.refresh()
  },
  destroyed () {
    clearInterval(this.intervalId)
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



